import {
  ADD_TO_CART,
  REMOVE_FROM_CART,
  GET_PUBLIC_PAGE_HEADER_INFO,
  CLEAR_CART,
  GET_PUBLIC_PAGE_USER,
} from '../../constants/actionTypes';

export default (state = {}, action) => {
  switch (action.type) {
    case ADD_TO_CART:
      if (state.cartItems && state.cartItems.length > 0) {
        let found = false;
        let temp = state.cartItems.map((c) => {
          if (c.item.id === action.payload.item.id && c.selectedSize === action.payload.selectedSize) {
            found = true;
            return { ...action.payload,quantity : action.payload.quantity + c.quantity};
          }
          return c;
        });
        if(!found ) {
          temp = [action.payload,...temp]
        }
        return { ...state, cartItems: temp };
      }
      return { ...state, cartItems: [action.payload] };
    case REMOVE_FROM_CART: {
      console.log(state.cartItems)
      const temp = state.cartItems.filter((c) => !((c.item.id === action.payload.item.id) && (c.selectedSize === action.payload.selectedSize)));
      return { ...state, cartItems: temp };
    }
    case CLEAR_CART:
      return {...state, cartItems:[]}
    case GET_PUBLIC_PAGE_HEADER_INFO: 
      return { ...state, publicHeaderInfo: action.payload.feature}
    case GET_PUBLIC_PAGE_USER:
      return {...state, publicPageUser: action.payload.user}
    default:
      return state;
  }

  return state;
};
