import React from 'react';
import Cropper from 'react-easy-crop';
import Modal from '@material-ui/core/Modal';
import imageCompression from 'browser-image-compression';

import { getOrientation } from '../../../node_modules/get-orientation/browser';
import getCroppedImg from './cropImage';
import Loader from './Loader';
import { getRotatedImage } from './rotateImage';
import './styles.css';

const ORIENTATION_TO_ANGLE = {
  3: 180,
  6: 90,
  8: -90,
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      maxSizeMB: 1,
      // maxWidthOrHeight: 1024,
      imageDataUrl: null,
      crop: { x: 0, y: 0 },
      zoom: 1,
      aspect: 4 / 3,
      croppedAreaPixels: null,
      croppedImageFile: null,
      loadingFile: false,
      loading: false,
    };
    this.compressImage = this.compressImage.bind(this);
  }


  componentWillMount() {
    const { isCroppingRequired, aspect } = this.props;
    this.setState({ isCroppingRequired });
  }

  onCropChange = (crop) => {
    this.setState({ crop });
  }


  onCropComplete = (croppedArea, croppedAreaPixels) => {
    this.setState({
      croppedAreaPixels,
    });
    this.showResult();
  }

  onZoomChange = (zoom) => {
    this.setState({ zoom });
  }

  showResult = async (pr) => {
    try {
      const croppedImageFile = await getCroppedImg(
        this.state.imageDataUrl,
        this.state.croppedAreaPixels,
        this.state.imgproperties,
      );

      this.setState({
        croppedImageFile,
        isCropping: false,

      });
    } catch (e) {
      this.setState({
        isCropping: false,
      });
    }
  }

  onClose = async () => {
    this.setState({
      croppedImageFile: null,
    });
  }

  onFileChange = async (e) => {
    if (e.target.files && e.target.files.length > 0) {
      const file = e.target.files[0];
      this.setState({ loadingFile: true });
      let imageDataUrl = await readFile(file);
      if (this.state.isCroppingRequired) {
        // apply rotation if needed
        const orientation = await getOrientation(file);
        const rotation = ORIENTATION_TO_ANGLE[orientation];
        if (rotation) {
          imageDataUrl = await getRotatedImage(imageDataUrl, rotation);
        }

        this.setState({
          tookInput: true,
          imageDataUrl,
          crop: { x: 0, y: 0 },
          zoom: 1,
          imgproperties: file,
          croppedImageFile: file,
          loadingFile: false,
        });
      } else {
        const imagePromise = this.compressImage( file, true);
        imagePromise.then((blob) => {
          this.props.onSelectFile({ blob, imageDataUrl });
        });
      }
    }
  }

  async compressImage(file, useWebWorker) {
    const options = {
      maxSizeMB: this.state.maxSizeMB,
      useWebWorker,
    };
    const output = await imageCompression(file, options);
    return output;
  }

  uploadFile() {
    // this.setState({ tookInput: false });
    const { croppedImageFile, imageDataUrl } = this.state;
    this.setState({loading:true})
    const imagePromise = this.compressImage(croppedImageFile, true);
    
    imagePromise.then((blob) => {
      this.setState({loading:false, tookInput:false})
      this.props.onSelectFile({ blob, imageDataUrl });
      
    });
  }

  render() {
    const {
      croppedImageFile, imageDataUrl, tookInput, crop, zoom, aspect, loading,
    } = this.state;
    return (
      <div>
        <div className="">
          <input
            type="file"
            id="avatar"
            name="avatar"
            accept="image/png, image/jpeg"
            className="select-img"
            onChange={this.onFileChange}
          />
        </div>
        <Modal
          open={tookInput}
          aria-labelledby="simple-modal-title"
          className="tc center "
          aria-describedby="simple-modal-description"
        >
          <div className="modal" style={{ position: 'relative', width: '100%', height: '100%' }}>
            <div className=" " style={{ position: 'relative', width: '100%', height: '100%' }}>
              <Cropper
                image={imageDataUrl}
                crop={crop}
                zoom={zoom}
                aspect={aspect}
                onCropChange={this.onCropChange}
                onCropComplete={this.onCropComplete}
                onZoomChange={this.onZoomChange}
              />
            </div>
            <button type="button" disabled={this.state.loadingFile} onClick={() => this.uploadFile()} className="color-btn  center mt0 pt0 mb0" style={{ height: '50px', zIndex: 50 }}> Upload Cropped Area</button>

            <button type="button" onClick={() => this.setState({ tookInput: false, cropped: false })} className="profile-btn white center mt0 pt0 mb0" style={{ height: '50px', zIndex: 50 }}>  Cancel</button>
            {
          loading? 
          <Loader/>:
          null
        }
          </div>
        </Modal>
        
      </div>

    );
  }
}

function readFile(file) {
  return new Promise((resolve) => {
    const reader = new FileReader();
    reader.addEventListener('load', () => resolve(reader.result), false);
    reader.readAsDataURL(file);
  });
}

export default App;
